import styled from 'styled-components';
import { Decor } from '../../components/decor/Decor';
import { FlexContainer } from '../../components/FlexContainer';
import { Icon } from '../../components/icon/Icon';
import { theme } from '../../style/Theme';
import decor from '../../assets/Decors/arrows(2).png';
import { Container } from '../../components/Container';

export const Footer = () => {
  return (
    <StyledFooter>
      <Container>
      <FlexContainer direction={'column'} align={'center'}>
      <Decor src={decor} bottom={'0px'} left={'120px'}/>
        <FooterList>
          <FooterItem>
            <FooterLink href="#">
              <Icon
                height={'35px'}
                width={'35px'}
                viewBox={'0 0 35 35'}
                iconId={'telegram'}
              />
            </FooterLink>
          </FooterItem>
          <FooterItem>
            <FooterLink href="#">
              <Icon
                height={'35px'}
                width={'35px'}
                viewBox={'0 0 35 35'}
                iconId={'linkedin'}
              />
            </FooterLink>
          </FooterItem>
          <FooterItem>
            <FooterLink href="#">
              <Icon
                height={'35px'}
                width={'35px'}
                viewBox={'0 0 35 35'}
                iconId={'email'}
              />
            </FooterLink>
          </FooterItem>
          <FooterItem>
            <FooterLink href="#">
              <Icon
                height={'35px'}
                width={'35px'}
                viewBox={'0 0 35 35'}
                iconId={'whatsapp'}
              />
            </FooterLink>
          </FooterItem>
        </FooterList>
      </FlexContainer>
      </Container>
    </StyledFooter>
  );
};
const StyledFooter = styled.footer`
  background-color: ${theme.color.primaryBg};
  padding: 100px 0 40px;
`;
const FooterList = styled.ul`
  display: flex;
  gap: 50px;
`;
const FooterItem = styled.li``;
const FooterLink = styled.a``;
